
.mainWrap{ width: 100%;margin: 0 auto; text-align: left; border-bottom: 1px solid #DEDEDE; background-color: #fff
}
.mainWrap .m_top,.m_bot{ padding: 5px 20px
}
.mainWrap .m_top img{ float: right; width: 100px;
}
.mainWrap .m_top h3 { font-size: 16px;
}
.mainWrap i{ width: 20px; height: 1px; background-color: #ccc; display: block; margin-top: 10px; margin-left: 20px
}
.mainWrap .m_top p { font-size: 14px; color: #999; padding-top: 10px; width: 65%;height: 60px;overflow : hidden;
text-overflow: ellipsis;
display: -webkit-box;
-webkit-line-clamp: 3;
-webkit-box-orient: vertical;
}
/*
-webkit-line-clamp用来限制在一个块元素显示的文本的行数。 为了实现该效果，它需要组合其他的
display: -webkit-box; 必须结合的属性 ，将对象作为弹性伸缩盒子模型显示 。
-webkit-box-orient 必须结合的属性 ，设置或检索伸缩盒对象的子元素的排列方式 。
text-overflow: ellipsis;，可以用来多行文本的情况下，用省略号“…”隐藏超出范围的文本 */
.mainWrap .m_bot { line-height: 40px; font-size: 12px; color: #999;padding-top: 20px
}
.mainWrap .m_bot .left{ float: left;
}
.mainWrap .m_bot .right{ float: right;
}



