
.groupList { width: 100%; height: 60px;padding-top: 20px; text-align: left; overflow: hidden;position: relative; margin: 0 auto
}
.groupList img { width: 50px; float: left; border-radius: 4px; padding-right: 10px
}
.groupList h5{ font-size: 16px
}
.groupList p{ font-size: 12px; color: #999
}
.groupList .jianjie{ width: 80%
}
.groupList span {position: absolute;top: 40px; right: 30px; font-size: 12px;color: #999
}
.groupList i { background-image: url(../assets/images/ic_group_check_anonymous.png); width: 20px; height: 20px; background-size: cover; position: absolute;top: 38px; right: 0px;
}
.groupList .i{ background-image: url(../assets/images/ic_group_checked_anonymous.png);
}



