
.brolistWrap { position: relative;box-shadow:2px 2px 3px rgba(0,0,0,.3); border-radius: 4px;margin-top: 10px
}
.brolistWrap .touxiang { width: 60px;float: left; padding: 10px 10px;padding-top: 20px;
}
.brolistWrap .cont { float: left; padding-top: 23px; text-align: left
}
.brolistWrap .cont h3{ font-size: 16px
}
.brolistWrap .cont span{ font-size: 12px; color: #999
}
.brolistWrap .cont p{ font-size: 12px
}
.brolistWrap .cont { width: 70%
}
.brolistWrap .cont img { width: 100%
}
.brolistWrap .guanbi {position: absolute;right: 7px; top: 0px; color: #999
}
.brolistWrap .guanzu { position: absolute; right: 20px; top: 20px;color: #fff; background-color: #1ABB2A; padding: 3px 15px; font-size: 12px; border-radius: 4px
}
.brolistWrap .guanzu2 { background-color: #fff; color: #ccc; background-image: url(../assets/images/ic_check_gray_small.png); background-position: -5px; background-size: 20px; background-repeat: no-repeat;
}



