
.mainlistWrap .top { height: 48px; background-color: #F3F2F2; width: 100%; border-bottom: 1px solid #ccc; text-align: left; line-height: 48px
}
.mainlistWrap .top img { width: 20px; padding-top: 15px; padding-right: 10px
}
.mainlistWrap .top .home { padding-left: 10px
}
.mainlistWrap .top .piclist{ float: right; list-style: none; height: 48px
}
.mainlistWrap .top .piclist li{ float: left;
}
.mainlistWrap .contWrap { padding: 10px; font-size: 14px;
}
.mainlistWrap .contWrap img { padding: 10px
}
.mainlistWrap .contWrap .left,.right,.desc{  padding-top: 10px;text-align: left;
}
.mainlistWrap .contWrap .desc{ text-indent: 2em;
}



