
.mineWrap .headerWrap{ border-bottom-color: #d2ebd6
}
.mineWrap .mine{ background-image: url(../../assets/images/2017060315491943509.png) ; width: 100%;height: 100px; background-repeat: no-repeat; background-size: cover; position: relative;
}
.mineWrap .touxiang { width: 60px; position: absolute; left: 30px; top: 20px
}
.mineWrap .denglu {position: absolute; left: 140px; top: 30px; padding: 10px 40px; border:1px solid rgba(255,255,255,.7); color: #fff
}
.mineWrap .remind{width: 100%; background-color: #fff; text-align: left;
}
.mineWrap .remind h3 {position: relative;font-size: 15px;color: #484848; padding: 10px 0; padding-left: 30px;border-bottom:1px solid #e3e3e3; margin-left: 10px
}
.mineWrap .remind h3 i{ background-repeat: no-repeat; width: 25px; height: 25px; background-size:contain; position: absolute; top:7px;
}
.mineWrap .remind h3 i:nth-of-type(1){ background-image: url(../../assets/images/ic_mine_notification.png);left: 0px;
}
.mineWrap .remind h3 i:nth-of-type(2){background-image: url(../../assets/images/ic_seti_arrow_right_gray.png); right: 10px; width: 15px;height: 15px;top: 10px
}
.mineWrap .remind p{ padding: 10px 0; text-align: center;font-size: 14px; color: #999
}
.mineWrap .top{ margin-top: 20px
}
.mineWrap .m_iconWrap { width: 100%; background-color: #fff;border-bottom: 1px solid #e3e3e3
}



